<template>
  <div class="about">
    <div class="top">
      <div style="display: flex; padding: 10px; width: 40px" @click="goBack">
        <i class="el-icon-back" style="color: #fff; cursor: pointer"></i>
      </div>
      <h1 class="title">{{ pageTitle }}</h1>
    </div>
    <div class="content">
      <el-card style="background: #ece8d9; width: 78%">
        <div>
          <div v-for="(item, i) in paginatedData" :key="i">
            <div class="itemContainer">
              <div class="textContainer">
                {{ item.topic }}
              </div>
              <div
                v-if="!isDown[i]"
                @click="downClick(i)"
                style="cursor: pointer; font-size: 32px"
              >
                <i class="el-icon-arrow-down"></i>
              </div>
              <div
                v-if="isDown[i]"
                @click="downClick(i)"
                style="cursor: pointer; font-size: 32px"
              >
                <i class="el-icon-arrow-up"></i>
              </div>
            </div>
            <div v-if="isDown[i]" class="summary">
              <el-card style="background: #b7e4c7">
                <el-descriptions :title="item.topic" :column="1">
                  <el-descriptions-item label="summary">{{
                    item.summary
                  }}</el-descriptions-item>
                </el-descriptions>
                <div>
                  <ul class="ul-item">
                    <li
                      class="li-item"
                      v-for="(i, index) in item.news_sources"
                      :key="index"
                    >
                      {{ i.title }}
                      <el-link
                        :href="'https://' + getDomain(i.url)"
                        target="_blank"
                        type="primary"
                        >{{ getDomain(i.url) }}</el-link
                      >
                      keyword:
                      <span
                        v-for="(kitem, k) in i.keywords"
                        @click="filterNewsByKeyword(data, kitem)"
                        style="
                          margin: 0px 5px 0px 5px;
                          color: #409eff;
                          border-bottom: 1px solid #409eff;
                          cursor: pointer;
                        "
                        :key="k"
                        >{{ kitem }}</span
                      >
                      Score :{{ i.bias }}
                      <el-popover
                        placement="top-start"
                        width="600"
                        trigger="hover"
                        content="Stay informed with concise summaries and objective bias ratings across business, entertainment, health, science, sports, and technology news. Each category is organized by topic for streamlined browsing, while bias is rated from 0-3 (left-leaning), 4-6 (neutral), and 7-10 (right-leaning). Access diverse perspectives and make informed decisions with ease."
                      >
                        <el-button slot="reference" type="text"
                          ><i class="el-icon-warning-outline"></i
                        ></el-button>
                      </el-popover>
                    </li>
                  </ul>
                </div>
              </el-card>
            </div>
          </div>
        </div>

        <div class="pagination">
          <button @click="prevPage" :disabled="currentPage === 1">
            ← previous
          </button>
          <span>page {{ currentPage }} </span>
          <button @click="nextPage" :disabled="currentPage === totalPages">
            next →
          </button>
        </div>
      </el-card>
      <el-card style="background: #ece8d9; width: 20%; margin-left: 20px">
        <div
          v-for="(item, index) in topKeywords"
          :key="index"
          style="margin-top: 15px; cursor: pointer"
          @click="filterNewsByKeyword(data, item.keyword)"
        >
          <div style="margin-bottom: 5px; font-weight: 700; font-size: 18px">
            {{ item.keyword }}
          </div>
          <div style="color: grey">count:{{ item.count }}</div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
// const data = require("../assets/data.json");
const data = require("../assets/final_output_1107.json");

export default {
  data() {
    return {
      pageTitle: this.$route.query.title,
      tableData: [],
      currentPage: 1,
      itemsPerPage: 5,
      isDown: [false, false, false, false, false],
      linkList: {},
      topKeywords: [],
      data: data,
    };
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
      this.isDown = [false, false, false, false, false];
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
      this.isDown = [false, false, false, false, false];
    },
    downClick(i) {
      this.$set(this.isDown, i, !this.isDown[i]);
    },
    getDomain(url) {
      const start = url.indexOf("://") + 3;
      const end = url.indexOf("/", start);
      return end === -1 ? url.substring(start) : url.substring(start, end);
    },
    filterNewsByKeyword(data, keyword) {
      const filteredNews = {
        item: [],
      };

      // 遍历每个大分类
      for (const category in data) {
        if (data[category]?.item) {
          // 遍历每个分类下的新闻数组
          data[category].item.forEach((newsItem) => {
            const relevantSources = newsItem.news_sources.filter((source) =>
              source.keywords.includes(keyword)
            );

            if (relevantSources.length > 0) {
              filteredNews.item.push({
                topic: newsItem.topic,
                summary: newsItem.summary,
                news_sources: newsItem.news_sources,
              });
            }
          });
        }
      }

      this.tableData = filteredNews;
      this.pageTitle = keyword;
    },
  },
  computed: {
    totalPages() {
      return Math.ceil(this.tableData.item.length / this.itemsPerPage);
    },
    paginatedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.tableData.item.slice(start, start + this.itemsPerPage);
    },
  },
  created() {
    switch (this.$route.query.title) {
      case "Business":
        this.tableData = data.business;
        break;
      case "Entertainment":
        this.tableData = data.entertainment;
        break;
      case "General":
        this.tableData = data.general;
        break;
      case "Health":
        this.tableData = data.health;
        break;
      case "Science":
        this.tableData = data.science;
        break;
      case "Sports":
        this.tableData = data.sports;
        break;
      case "Technology":
        this.tableData = data.technology;
        break;
      default:
        this.filterNewsByKeyword(data, this.$route.query.title);
    }

    const getAllKeywords = (data) => {
      const allKeywords = [];
      Object.values(data).forEach((category) => {
        category.item.forEach((topic) => {
          topic.news_sources.forEach((news) => {
            allKeywords.push(...news.keywords);
          });
        });
      });
      return allKeywords;
    };

    const countKeywords = (keywords) => {
      return keywords.reduce((countMap, keyword) => {
        countMap[keyword] = (countMap[keyword] || 0) + 1;
        return countMap;
      }, {});
    };

    const getTopKeywords = (keywordCount, topN = 10) => {
      return Object.entries(keywordCount)
        .sort((a, b) => b[1] - a[1]) // 按出现次数从高到低排序
        .slice(0, topN) // 取前 topN 个
        .map(([keyword, count]) => ({ keyword, count }));
    };

    const allKeywords = getAllKeywords(data);
    const keywordCount = countKeywords(allKeywords);
    const topKeywords = getTopKeywords(keywordCount);
    this.topKeywords = topKeywords;
  },
};
</script>

